"use client";

import React, { useEffect, useState } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Badge } from "@/components/ui/badge";
import { Slider } from "@/components/ui/slider";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Progress } from "@/components/ui/progress";
import DynamicChart from "@/components/DynamicChart";

interface SimulationData {
  input_parameters: {
    market_intensity: number;
    competition_level: number;
    inventory_level: number;
    seasonal_factor: number;
  };
  predicted_price: number;
  recommendation: string;
  strategy: string;
  market_trend: string;
  confidence_score: number;
  profit_margin: number;
  demand_elasticity: number;
  model_insights: {
    linear_regression_price: number;
    random_forest_price: number;
    price_range: {
      min: number;
      max: number;
    };
  };
  timestamp: string;
}

interface MarketDataPoint {
  date: string;
  price: number;
  market_intensity: number;
  competition_level: number;
  inventory_level: number;
  demand: number;
}

interface AnalyticsData {
  total_simulations: number;
  average_price: number;
  price_trend: string;
  market_volatility: number;
  top_strategies: Array<{
    strategy: string;
    usage: number;
  }>;
  performance_metrics: {
    accuracy: number;
    confidence: number;
    response_time: string;
  };
}

export default function SimulatorPage() {
  const [marketIntensity, setMarketIntensity] = useState([50]);
  const [competitionLevel, setCompetitionLevel] = useState([0.5]);
  const [inventoryLevel, setInventoryLevel] = useState([50]);
  const [seasonalFactor, setSeasonalFactor] = useState([0]);
  
  const [simulationData, setSimulationData] = useState<SimulationData | null>(null);
  const [marketData, setMarketData] = useState<MarketDataPoint[]>([]);
  const [analyticsData, setAnalyticsData] = useState<AnalyticsData | null>(null);
  const [error, setError] = useState<string>("");
  const [loading, setLoading] = useState(false);
  const [autoRefresh, setAutoRefresh] = useState(true);

  const API_BASE_URL = "http://localhost:5000";

  // Function to run simulation
  const runSimulation = async () => {
    try {
      setLoading(true);
      setError("");
      
      const params = new URLSearchParams({
        market_intensity: marketIntensity[0].toString(),
        competition_level: competitionLevel[0].toString(),
        inventory_level: inventoryLevel[0].toString(),
        seasonal_factor: seasonalFactor[0].toString(),
      });

      const response = await fetch(`${API_BASE_URL}/simulate-quick?${params}`);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      setSimulationData(data);
    } catch (err: any) {
      console.error("Simulation error:", err);
      setError(err.message || "Failed to run simulation. Make sure the backend server is running on port 5000.");
    } finally {
      setLoading(false);
    }
  };

  // Function to fetch market data
  const fetchMarketData = async () => {
    try {
      const response = await fetch(`${API_BASE_URL}/market-data?days=30`);
      if (response.ok) {
        const data = await response.json();
        setMarketData(data.data || []);
      } else {
        console.warn("Market data endpoint not available, using mock data");
        // Use mock market data as fallback
        const mockData = Array.from({ length: 30 }, (_, i) => ({
          date: new Date(Date.now() - (29 - i) * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
          price: 100 + Math.random() * 50 + Math.sin(i * 0.2) * 10,
          market_intensity: 50 + Math.random() * 20,
          competition_level: 0.3 + Math.random() * 0.4,
          inventory_level: 60 + Math.random() * 30,
          demand: 50 + Math.random() * 25
        })).map(d => ({
          ...d,
          price: Math.round(d.price * 100) / 100,
          market_intensity: Math.round(d.market_intensity * 10) / 10,
          competition_level: Math.round(d.competition_level * 100) / 100,
          inventory_level: Math.round(d.inventory_level * 10) / 10,
          demand: Math.round(d.demand * 10) / 10
        }));
        setMarketData(mockData);
      }
    } catch (err) {
      console.error("Market data error:", err);
      // Use mock market data as fallback
      const mockData = Array.from({ length: 30 }, (_, i) => ({
        date: new Date(Date.now() - (29 - i) * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
        price: 100 + Math.random() * 50 + Math.sin(i * 0.2) * 10,
        market_intensity: 50 + Math.random() * 20,
        competition_level: 0.3 + Math.random() * 0.4,
        inventory_level: 60 + Math.random() * 30,
        demand: 50 + Math.random() * 25
      })).map(d => ({
        ...d,
        price: Math.round(d.price * 100) / 100,
        market_intensity: Math.round(d.market_intensity * 10) / 10,
        competition_level: Math.round(d.competition_level * 100) / 100,
        inventory_level: Math.round(d.inventory_level * 10) / 10,
        demand: Math.round(d.demand * 10) / 10
      }));
      setMarketData(mockData);
    }
  };

  // Function to fetch analytics
  const fetchAnalytics = async () => {
    try {
      const response = await fetch(`${API_BASE_URL}/analytics`);
      if (response.ok) {
        const data = await response.json();
        setAnalyticsData(data);
      } else {
        console.warn("Analytics endpoint not available, using mock data");
        // Use mock analytics data as fallback
        setAnalyticsData({
          total_simulations: 1247,
          average_price: 98.45,
          price_trend: "increasing",
          market_volatility: 0.23,
          top_strategies: [
            { strategy: "premium", usage: 45.2 },
            { strategy: "balanced", usage: 38.7 },
            { strategy: "competitive", usage: 16.1 }
          ],
          performance_metrics: {
            accuracy: 0.87,
            confidence: 0.92,
            response_time: "120ms"
          }
        });
      }
    } catch (err) {
      console.error("Analytics error:", err);
      // Use mock analytics data as fallback
      setAnalyticsData({
        total_simulations: 1247,
        average_price: 98.45,
        price_trend: "increasing",
        market_volatility: 0.23,
        top_strategies: [
          { strategy: "premium", usage: 45.2 },
          { strategy: "balanced", usage: 38.7 },
          { strategy: "competitive", usage: 16.1 }
        ],
        performance_metrics: {
          accuracy: 0.87,
          confidence: 0.92,
          response_time: "120ms"
        }
      });
    }
  };

  // Initial load and auto-refresh setup
  useEffect(() => {
    runSimulation();
    fetchMarketData();
    fetchAnalytics();
  }, []);

  useEffect(() => {
    if (autoRefresh) {
      const interval = setInterval(() => {
        runSimulation();
        fetchAnalytics();
      }, 15000); // Refresh every 15 seconds
      
      return () => clearInterval(interval);
    }
  }, [autoRefresh, marketIntensity, competitionLevel, inventoryLevel, seasonalFactor]);

  const getStrategyColor = (strategy: string) => {
    switch (strategy) {
      case "premium":
        return "bg-green-100 text-green-800 border-green-200";
      case "competitive":
        return "bg-red-100 text-red-800 border-red-200";
      case "balanced":
        return "bg-blue-100 text-blue-800 border-blue-200";
      default:
        return "bg-gray-100 text-gray-800 border-gray-200";
    }
  };

  return (
    <div className="min-h-screen bg-background p-4">
      {/* Header */}
      <header className="mb-8">
        <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between">
          <div>
            <h1 className="text-4xl font-bold text-foreground">Retail Pricing Simulator</h1>
            <p className="text-muted-foreground mt-2">
              AI-powered dynamic pricing optimization platform
            </p>
          </div>
          <div className="flex items-center space-x-4 mt-4 sm:mt-0">
            <Button
              variant={autoRefresh ? "default" : "outline"}
              onClick={() => setAutoRefresh(!autoRefresh)}
              size="sm"
            >
              {autoRefresh ? "Auto-Refresh ON" : "Auto-Refresh OFF"}
            </Button>
            <Button onClick={runSimulation} disabled={loading} size="sm">
              {loading ? "Running..." : "Run Simulation"}
            </Button>
          </div>
        </div>
      </header>

      {/* Error Alert */}
      {error && (
        <Alert className="mb-6 border-destructive">
          <AlertDescription className="text-destructive">
            {error}
          </AlertDescription>
        </Alert>
      )}

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Control Panel */}
        <div className="lg:col-span-1">
          <Card>
            <CardHeader>
              <CardTitle>Simulation Parameters</CardTitle>
              <CardDescription>
                Adjust market conditions to see pricing recommendations
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <div>
                <label className="block text-sm font-medium mb-2">
                  Market Intensity: {marketIntensity[0]}
                </label>
                <Slider
                  value={marketIntensity}
                  onValueChange={setMarketIntensity}
                  max={100}
                  min={0}
                  step={1}
                  className="w-full"
                />
                <p className="text-xs text-muted-foreground mt-1">
                  Higher values indicate stronger market demand
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium mb-2">
                  Competition Level: {competitionLevel[0].toFixed(2)}
                </label>
                <Slider
                  value={competitionLevel}
                  onValueChange={setCompetitionLevel}
                  max={1}
                  min={0}
                  step={0.01}
                  className="w-full"
                />
                <p className="text-xs text-muted-foreground mt-1">
                  0 = Low competition, 1 = High competition
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium mb-2">
                  Inventory Level: {inventoryLevel[0]}
                </label>
                <Slider
                  value={inventoryLevel}
                  onValueChange={setInventoryLevel}
                  max={100}
                  min={0}
                  step={1}
                  className="w-full"
                />
                <p className="text-xs text-muted-foreground mt-1">
                  Current stock levels (0-100)
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium mb-2">
                  Seasonal Factor: {seasonalFactor[0].toFixed(1)}
                </label>
                <Slider
                  value={seasonalFactor}
                  onValueChange={setSeasonalFactor}
                  max={10}
                  min={-10}
                  step={0.1}
                  className="w-full"
                />
                <p className="text-xs text-muted-foreground mt-1">
                  Seasonal demand adjustment (-10 to +10)
                </p>
              </div>
            </CardContent>
          </Card>

          {/* Analytics Summary */}
          {analyticsData && (
            <Card className="mt-6">
              <CardHeader>
                <CardTitle>Analytics Summary</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="grid grid-cols-2 gap-4 text-sm">
                  <div>
                    <p className="text-muted-foreground">Total Simulations</p>
                    <p className="font-semibold">{analyticsData.total_simulations}</p>
                  </div>
                  <div>
                    <p className="text-muted-foreground">Avg Price</p>
                    <p className="font-semibold">${analyticsData.average_price}</p>
                  </div>
                  <div>
                    <p className="text-muted-foreground">Accuracy</p>
                    <p className="font-semibold">{(analyticsData.performance_metrics.accuracy * 100).toFixed(1)}%</p>
                  </div>
                  <div>
                    <p className="text-muted-foreground">Response Time</p>
                    <p className="font-semibold">{analyticsData.performance_metrics.response_time}</p>
                  </div>
                </div>
              </CardContent>
            </Card>
          )}
        </div>

        {/* Main Content */}
        <div className="lg:col-span-2">
          {simulationData ? (
            <div className="space-y-6">
              {/* Key Metrics */}
              <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle className="text-lg">Predicted Price</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="text-3xl font-bold text-primary">
                      ${simulationData.predicted_price}
                    </div>
                    <p className="text-sm text-muted-foreground mt-1">
                      Range: ${simulationData.model_insights.price_range.min} - ${simulationData.model_insights.price_range.max}
                    </p>
                  </CardContent>
                </Card>

                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle className="text-lg">Strategy</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <Badge className={getStrategyColor(simulationData.strategy)}>
                      {simulationData.strategy.toUpperCase()}
                    </Badge>
                    <p className="text-sm text-muted-foreground mt-2">
                      {simulationData.market_trend}
                    </p>
                  </CardContent>
                </Card>

                <Card>
                  <CardHeader className="pb-2">
                    <CardTitle className="text-lg">Confidence</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <div className="text-2xl font-bold">
                      {(simulationData.confidence_score * 100).toFixed(1)}%
                    </div>
                    <Progress 
                      value={simulationData.confidence_score * 100} 
                      className="mt-2"
                    />
                  </CardContent>
                </Card>
              </div>

              {/* Recommendation */}
              <Card>
                <CardHeader>
                  <CardTitle>Pricing Recommendation</CardTitle>
                </CardHeader>
                <CardContent>
                  <p className="text-lg mb-4">{simulationData.recommendation}</p>
                  <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 text-sm">
                    <div>
                      <p className="text-muted-foreground">Profit Margin</p>
                      <p className="font-semibold text-lg">{simulationData.profit_margin.toFixed(1)}%</p>
                    </div>
                    <div>
                      <p className="text-muted-foreground">Demand Elasticity</p>
                      <p className="font-semibold text-lg">{simulationData.demand_elasticity.toFixed(3)}</p>
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* Charts */}
              <Card>
                <CardHeader>
                  <CardTitle>Data Visualization</CardTitle>
                </CardHeader>
                <CardContent>
                  <Tabs defaultValue="price-trend" className="w-full">
                    <TabsList className="grid w-full grid-cols-3">
                      <TabsTrigger value="price-trend">Price Trend</TabsTrigger>
                      <TabsTrigger value="market-factors">Market Factors</TabsTrigger>
                      <TabsTrigger value="price-comparison">Model Comparison</TabsTrigger>
                    </TabsList>
                    
                    <TabsContent value="price-trend" className="mt-4">
                      <DynamicChart
                        simulationData={simulationData}
                        marketData={marketData}
                        chartType="price-trend"
                      />
                    </TabsContent>
                    
                    <TabsContent value="market-factors" className="mt-4">
                      <DynamicChart
                        simulationData={simulationData}
                        marketData={marketData}
                        chartType="market-factors"
                      />
                    </TabsContent>
                    
                    <TabsContent value="price-comparison" className="mt-4">
                      <DynamicChart
                        simulationData={simulationData}
                        marketData={marketData}
                        chartType="price-comparison"
                      />
                    </TabsContent>
                  </Tabs>
                </CardContent>
              </Card>
            </div>
          ) : (
            <Card>
              <CardContent className="flex items-center justify-center h-64">
                <div className="text-center">
                  <div className="text-muted-foreground mb-4">
                    {loading ? "Running simulation..." : "No simulation data available"}
                  </div>
                  {!loading && (
                    <Button onClick={runSimulation}>
                      Run Initial Simulation
                    </Button>
                  )}
                </div>
              </CardContent>
            </Card>
          )}
        </div>
      </div>
    </div>
  );
}
