"use client";

import React, { useRef, useEffect } from "react";
import * as d3 from "d3";

interface SimulationData {
  input_parameters: {
    market_intensity: number;
    competition_level: number;
    inventory_level: number;
    seasonal_factor: number;
  };
  predicted_price: number;
  recommendation: string;
  strategy: string;
  market_trend: string;
  confidence_score: number;
  profit_margin: number;
  demand_elasticity: number;
  model_insights: {
    linear_regression_price: number;
    random_forest_price: number;
    price_range: {
      min: number;
      max: number;
    };
  };
  timestamp: string;
}

interface MarketDataPoint {
  date: string;
  price: number;
  market_intensity: number;
  competition_level: number;
  inventory_level: number;
  demand: number;
}

interface DynamicChartProps {
  simulationData: SimulationData | null;
  marketData: MarketDataPoint[];
  chartType: "price-trend" | "market-factors" | "price-comparison";
}

const DynamicChart: React.FC<DynamicChartProps> = ({ 
  simulationData, 
  marketData, 
  chartType = "price-trend" 
}) => {
  const svgRef = useRef<SVGSVGElement | null>(null);

  useEffect(() => {
    if (!svgRef.current) return;

    const svg = d3.select(svgRef.current);
    svg.selectAll("*").remove(); // Clear previous chart

    const width = 600;
    const height = 300;
    const margin = { top: 20, right: 30, bottom: 40, left: 50 };

    svg
      .attr("width", width)
      .attr("height", height)
      .attr("class", "bg-card rounded border border-border");

    if (chartType === "price-trend" && marketData.length > 0) {
      drawPriceTrendChart(svg, marketData, width, height, margin);
    } else if (chartType === "market-factors" && simulationData) {
      drawMarketFactorsChart(svg, simulationData, width, height, margin);
    } else if (chartType === "price-comparison" && simulationData) {
      drawPriceComparisonChart(svg, simulationData, width, height, margin);
    }
  }, [simulationData, marketData, chartType]);

  const drawPriceTrendChart = (
    svg: d3.Selection<SVGSVGElement, unknown, null, undefined>,
    data: MarketDataPoint[],
    width: number,
    height: number,
    margin: { top: number; right: number; bottom: number; left: number }
  ) => {
    const parseDate = d3.timeParse("%Y-%m-%d");
    const processedData = data.map(d => ({
      ...d,
      parsedDate: parseDate(d.date)!
    }));

    const xScale = d3
      .scaleTime()
      .domain(d3.extent(processedData, d => d.parsedDate) as [Date, Date])
      .range([margin.left, width - margin.right]);

    const yScale = d3
      .scaleLinear()
      .domain(d3.extent(processedData, d => d.price) as [number, number])
      .nice()
      .range([height - margin.bottom, margin.top]);

    // Add axes
    svg
      .append("g")
      .attr("transform", `translate(0,${height - margin.bottom})`)
      .call(d3.axisBottom(xScale).tickFormat(d3.timeFormat("%m/%d") as any))
      .attr("color", "var(--muted-foreground)");

    svg
      .append("g")
      .attr("transform", `translate(${margin.left},0)`)
      .call(d3.axisLeft(yScale))
      .attr("color", "var(--muted-foreground)");

    // Add axis labels
    svg
      .append("text")
      .attr("transform", "rotate(-90)")
      .attr("y", 0 - margin.left)
      .attr("x", 0 - (height / 2))
      .attr("dy", "1em")
      .style("text-anchor", "middle")
      .style("fill", "var(--muted-foreground)")
      .style("font-size", "12px")
      .text("Price ($)");

    svg
      .append("text")
      .attr("transform", `translate(${width / 2}, ${height - 5})`)
      .style("text-anchor", "middle")
      .style("fill", "var(--muted-foreground)")
      .style("font-size", "12px")
      .text("Date");

    // Create line generator
    const line = d3
      .line<typeof processedData[0]>()
      .x(d => xScale(d.parsedDate))
      .y(d => yScale(d.price))
      .curve(d3.curveMonotoneX);

    // Add the line
    svg
      .append("path")
      .datum(processedData)
      .attr("fill", "none")
      .attr("stroke", "var(--primary)")
      .attr("stroke-width", 2)
      .attr("d", line);

    // Add data points
    svg
      .selectAll(".dot")
      .data(processedData)
      .enter()
      .append("circle")
      .attr("class", "dot")
      .attr("cx", d => xScale(d.parsedDate))
      .attr("cy", d => yScale(d.price))
      .attr("r", 3)
      .attr("fill", "var(--primary)")
      .on("mouseover", function(event, d) {
        // Simple tooltip
        const tooltip = svg
          .append("g")
          .attr("id", "tooltip");

        tooltip
          .append("rect")
          .attr("x", xScale(d.parsedDate) + 10)
          .attr("y", yScale(d.price) - 30)
          .attr("width", 80)
          .attr("height", 25)
          .attr("fill", "var(--popover)")
          .attr("stroke", "var(--border)")
          .attr("rx", 4);

        tooltip
          .append("text")
          .attr("x", xScale(d.parsedDate) + 15)
          .attr("y", yScale(d.price) - 10)
          .style("fill", "var(--popover-foreground)")
          .style("font-size", "12px")
          .text(`$${d.price.toFixed(2)}`);
      })
      .on("mouseout", function() {
        svg.select("#tooltip").remove();
      });
  };

  const drawMarketFactorsChart = (
    svg: d3.Selection<SVGSVGElement, unknown, null, undefined>,
    data: SimulationData,
    width: number,
    height: number,
    margin: { top: number; right: number; bottom: number; left: number }
  ) => {
    const factors = [
      { name: "Market Intensity", value: data.input_parameters.market_intensity },
      { name: "Competition", value: data.input_parameters.competition_level * 100 },
      { name: "Inventory Level", value: data.input_parameters.inventory_level },
      { name: "Seasonal Factor", value: Math.abs(data.input_parameters.seasonal_factor) * 10 }
    ];

    const xScale = d3
      .scaleBand()
      .domain(factors.map(d => d.name))
      .range([margin.left, width - margin.right])
      .padding(0.1);

    const yScale = d3
      .scaleLinear()
      .domain([0, d3.max(factors, d => d.value) || 100])
      .nice()
      .range([height - margin.bottom, margin.top]);

    // Add axes
    svg
      .append("g")
      .attr("transform", `translate(0,${height - margin.bottom})`)
      .call(d3.axisBottom(xScale))
      .attr("color", "var(--muted-foreground)")
      .selectAll("text")
      .style("text-anchor", "end")
      .attr("dx", "-.8em")
      .attr("dy", ".15em")
      .attr("transform", "rotate(-45)");

    svg
      .append("g")
      .attr("transform", `translate(${margin.left},0)`)
      .call(d3.axisLeft(yScale))
      .attr("color", "var(--muted-foreground)");

    // Add bars
    svg
      .selectAll(".bar")
      .data(factors)
      .enter()
      .append("rect")
      .attr("class", "bar")
      .attr("x", d => xScale(d.name)!)
      .attr("y", d => yScale(d.value))
      .attr("width", xScale.bandwidth())
      .attr("height", d => height - margin.bottom - yScale(d.value))
      .attr("fill", "var(--primary)")
      .attr("opacity", 0.8);

    // Add value labels on bars
    svg
      .selectAll(".label")
      .data(factors)
      .enter()
      .append("text")
      .attr("class", "label")
      .attr("x", d => xScale(d.name)! + xScale.bandwidth() / 2)
      .attr("y", d => yScale(d.value) - 5)
      .attr("text-anchor", "middle")
      .style("fill", "var(--foreground)")
      .style("font-size", "12px")
      .text(d => d.value.toFixed(1));
  };

  const drawPriceComparisonChart = (
    svg: d3.Selection<SVGSVGElement, unknown, null, undefined>,
    data: SimulationData,
    width: number,
    height: number,
    margin: { top: number; right: number; bottom: number; left: number }
  ) => {
    const priceData = [
      { model: "Linear Regression", price: data.model_insights.linear_regression_price },
      { model: "Random Forest", price: data.model_insights.random_forest_price },
      { model: "Final Prediction", price: data.predicted_price }
    ];

    const xScale = d3
      .scaleBand()
      .domain(priceData.map(d => d.model))
      .range([margin.left, width - margin.right])
      .padding(0.2);

    const yScale = d3
      .scaleLinear()
      .domain([0, d3.max(priceData, d => d.price) || 100])
      .nice()
      .range([height - margin.bottom, margin.top]);

    // Add axes
    svg
      .append("g")
      .attr("transform", `translate(0,${height - margin.bottom})`)
      .call(d3.axisBottom(xScale))
      .attr("color", "var(--muted-foreground)");

    svg
      .append("g")
      .attr("transform", `translate(${margin.left},0)`)
      .call(d3.axisLeft(yScale))
      .attr("color", "var(--muted-foreground)");

    // Add bars with different colors
    const colors = ["var(--chart-1)", "var(--chart-2)", "var(--primary)"];
    
    svg
      .selectAll(".bar")
      .data(priceData)
      .enter()
      .append("rect")
      .attr("class", "bar")
      .attr("x", d => xScale(d.model)!)
      .attr("y", d => yScale(d.price))
      .attr("width", xScale.bandwidth())
      .attr("height", d => height - margin.bottom - yScale(d.price))
      .attr("fill", (d, i) => colors[i])
      .attr("opacity", 0.8);

    // Add value labels
    svg
      .selectAll(".label")
      .data(priceData)
      .enter()
      .append("text")
      .attr("class", "label")
      .attr("x", d => xScale(d.model)! + xScale.bandwidth() / 2)
      .attr("y", d => yScale(d.price) - 5)
      .attr("text-anchor", "middle")
      .style("fill", "var(--foreground)")
      .style("font-size", "12px")
      .style("font-weight", "bold")
      .text(d => `$${d.price.toFixed(2)}`);
  };

  const getChartTitle = () => {
    switch (chartType) {
      case "price-trend":
        return "Price Trend Over Time";
      case "market-factors":
        return "Market Factors Analysis";
      case "price-comparison":
        return "Model Price Comparison";
      default:
        return "Chart";
    }
  };

  return (
    <div className="w-full">
      <h3 className="text-lg font-semibold mb-4 text-foreground">
        {getChartTitle()}
      </h3>
      <div className="flex justify-center">
        <svg ref={svgRef}></svg>
      </div>
    </div>
  );
};

export default DynamicChart;
